import { Component, Input, OnInit } from '@angular/core';
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ModalsvcService } from 'src/app/service/modalsvc.service';
import { SharedataService } from 'src/app/service/sharedata.service';
@Component({
  selector: 'app-modal',
  templateUrl: './modal.component.html',
  styleUrls: ['./modal.component.css']
})
export class ModalComponent implements OnInit {


  data:any;
  cartArray=[];
  quantity:number=1;
  constructor(public activeModal: NgbActiveModal,private modalsvc:ModalsvcService,private sharedatasvc:SharedataService) { 

  }

  ngOnInit(): void {
    let observable = this.modalsvc.getData();
    observable.subscribe((data) => {
      this.data = data;
    });
  }

  addtocart(val:any)
  {  
    val['quantity']=this.quantity;
    localStorage.setItem(val.id,JSON.stringify(val));
    this.activeModal.close();
  }
}
