import { Component, OnInit } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { of } from 'rxjs';
import { DatasvcService } from 'src/app/service/datasvc.service';
import { ModalsvcService } from 'src/app/service/modalsvc.service';
import { ModalComponent } from '../modal/modal.component';


@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  productData:Array<any>=[];
  selectedCat:Array<any>=[];
  temp:Array<any>=[];
  changeCat:boolean=false;
  searchText="";
  constructor(private dsvc:DatasvcService,private modalService: NgbModal,private modalsvc:ModalsvcService) { }

  ngOnInit(): void {
    this.dsvc.getData().subscribe(data=>{
      setTimeout(() => {
        this.productData=data;
      }, 100);
    });
  }

  selectCategory(val: any) {
    if (val !== 'all') {
      if (this.changeCat) {
        this.productData = this.temp;
        this.selectedCat = [];
      }
      this.productData.forEach(element => {
        if (element.category === val) {
          this.changeCat = true;
          this.selectedCat.push(element);
        }
      });

      this.temp = this.productData;
      this.productData = this.selectedCat;
    }
    else
      this.productData = this.temp;
  }

  openmodal(item:any) {
    const modalRef = this.modalService.open(ModalComponent,item);
    this.modalsvc.setData({id:item.id,title:item.title,price:item.price,image:item.image});
  }

}
